body {
  overflow: hidden; /* no scrollbars! */
  font-family: Arial, Helvetica, sans-serif;
}

#canvas {
	display: block;
	height: 100%;
	width: 100%;
}

#canvas-container {
  position: absolute;
  width: 100vw;
  /* height: 70vh; */
  top: 0;
}

#editor-drag-bar {
  width: 100vw;
  height: 3px;
  background-color: red;
  cursor: ns-resize;
}

#editor-container {
  position: absolute;
  width: 100vw;
  /* height: 30vh; */
  bottom: 0;
  background-color: #1e1e1e; /* monaco dark is #1e1e1e */
}

#tab-bar {
  cursor: default;
}

#tab-bar > .tab-item {
  display: inline-block;
  background-color: #373737;
  padding: 5px 15px;
  color: white;
  cursor: pointer;
  font-size: 12px;
}

#tab-bar > .tab-item.selected {
  background-color: #222;
  cursor: default;
}

#editor {
  position: absolute;
  width: 100vw;
  height: calc(100% - 26px);
  bottom: 0;
}
